<template>
	<div class="groupWarg">
		<Topheader></Topheader>
		<Myheader  txt = '' @myseek = myseeks>
			<img src="../../assets/images/ic_chat_green.png" slot = 'right'/>
			<img src="../../assets/images/ic_actionbar_search_icon.png" slot = 'left'/>
		</Myheader>
		<Groupinner></Groupinner>
		<Groupfilms @chenging = 'chengs'>
			<img src="../../assets/images/pic_g_img_09.jpg" slot = 'groupfilms_img'/>
			<h1 slot = 'gtou_txt_h'>...</h1>
			<p slot = 'gtou_txt_p'>...</p>
			<span slot = 'groupfilms_num'>612808</span>
		</Groupfilms>
		<Groupfilms @chenging = 'chengs'>
			<img src="../../assets/images/pic_g_img_12.jpg" slot = 'groupfilms_img'/>
			<h1 slot = 'gtou_txt_h'>The Criterion Collection</h1>
			<p slot = 'gtou_txt_p'>Criterion CollectionDVD</p>
			<span slot = 'groupfilms_num'>6060</span>
		</Groupfilms>
		<Groupfilms @chenging = 'chengs'>
			<img src="../../assets/images/b_pic_07.jpg" slot = 'groupfilms_img'/>
			<h1 slot = 'gtou_txt_h'></h1>
			<p slot = 'gtou_txt_p'>FRIENDS</p>
			<span slot = 'groupfilms_num'>65145</span>
		</Groupfilms>
		<Groupfilms @chenging = 'chengs'>
			<img src="../../assets/images/b_pic_10.jpg" slot = 'groupfilms_img'/>
			<h1 slot = 'gtou_txt_h'></h1>
			<p slot = 'gtou_txt_p'></p>
			<span slot = 'groupfilms_num'>7551</span>
		</Groupfilms>
		<Batch>
			<div slot = 'advertising' :class="{coloring : vuling == false ?true:false}"></div>
		</Batch>
		<Seek :seekShows = 'mySeek' @seeks = 'seekmy'></Seek>
	</div>
</template>

<script>
	import Myheader from '../../components/myheader'
	import Topheader from '../../components/topheader'
	import Groupinner from './groupinner'
	import Groupfilms from './groupfilms'
	import Batch from '../../components/batch'
	import Seek from '../../components/seek'
	
	export default {
		components :{
			Myheader:Myheader,
			Topheader,
			Groupinner,
			Groupfilms,
			Batch,
			Seek
		},
		data : function(){
			return {
				vuling : true,
				mySeek : ''
			}
		},
		methods : {
			chengs:function(vul){
				this.vuling = vul ;
			},
			myseeks : function(myvul){
				this.mySeek = myvul;
			},
			seekmy : function(vuls){
				this.mySeek = vuls;
			}
		}
	}
</script>

<style>
.groupWarg{min-height: 11.85rem;padding:0.8rem 0 2.3rem 0;}
.groupWarg .coloring{background: #42C055;}
.groupWarg .seekAll .homeTops{background: #F7F8F7;color: #999;}
.groupWarg .seekAll .homeTops span{color:#999;}
</style>


